.card{
  display: flex;
    justify-content: space-around;
    align-items: center;

    margin-top: 50px;

    background-color: rgb(240, 240, 240);
    
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 20px;
    box-shadow: 7px 10px 15px -3px rgba(0,0,0,0.1);

    padding: 20px;

    font-weight: bold;

    cursor: pointer;
}

.card:hover{
  background-color: rgb(228, 228, 228);
}

.features button{
  width: 100px;
  height: 30px;

  border: none;
  border-radius: 20px;

  background-color: #017828;

  color: white;
  cursor: pointer;
}

