.headerCertificatePanel{
  font-family: var(--inter);
  opacity: 0.8;
  font-size: 1.3rem;

  margin-bottom: 50px;
}

.bodyCertificatePanel{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  width: 850px;

  margin: 0 auto;
}

.CertificateBox{
  width: 250px;
  height: 150px;
  padding: 5px;

  background-color: rgb(229, 229, 229);

  border-radius: 10px;

  margin-left: 20px;
  margin-bottom: 20px;

  cursor: pointer;
}

.CertificateBox:hover{
  background-color: rgb(212, 212, 212);
}

.headerCertificateBox p{
  font-size: 3rem;
}

.bodyCertificateBox p:nth-child(1){
  font-family: var(--inter);
}

.modalContainerCertificate{
  width: 100%;
  height: 100vh;

  padding: 10px;

  display: flex;
  flex-wrap: wrap;
  align-items: center;

  overflow-y: auto;
  background-color: white;
}

.downloadCertificate{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;

  width: 100%;
}

.downloadButton{
  margin: 20px auto;

  width: 200px;
  height: 50px;

  background-color: #017828;
  color: white;

  border-radius: 5px;

  padding: 5px;

  cursor: pointer;
}

.pdfViewerContainer{
  width: 1000px;
  height: 500px;
}

.closeModal{
  position: absolute;
  top: 10px;
  right: 10px;
}

@media (max-width: 700px){
  .CertificateBox{
    width: 100%;

    margin-left: 0;
  }
  .bodyCertificatePanel{
    width: 100%;

    padding: 10px;
  }
  .bodyCertificateBox p{
    font-family: var(--inter);

    font-size: 1.2rem;
  }
  .ModalCertificateBox{
    width: 100%;
    height: 100%;
    
    background-color: white;  
  }
  .downloadButton{
    width: 100%;
    margin-top: 50px;
  }
  .pdfViewerContainer{
    width: 100%;
    height: 200px;
  }
  .closeModal{
    left: 10px;

    margin-bottom: 20px;
  }
}

@media (max-width: 1115px){
  .bodyCertificatePanel{
    width: 100%;

    padding: 10px;
  }
}