.controlPanel{
  width: 70%;
  height: auto;

  margin: 50px auto;

  background-color: white;
  border-radius: 15px;
  padding-bottom: 20px;
}

.headerPanel{
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  
  width: 100%;
  height: 100px;

  border-top-left-radius: 15px;
  border-top-right-radius: 15px;

  color: white;
  background-color: #386641;
}

.iconPanel{
  position: absolute;
  bottom: -45px;
  display: flex;
  align-items: center;
  justify-content: center;

  width: 90px;
  border-radius: 50px;
  background-color: #386641;
  font-size: 4rem;
}

.bodyPanel{
  margin-top: 50px;
  width:100%;
  text-align: center;
}


/*PANEL MOBILE */

@media (max-width: 700px){
  .controlPanel{
      margin: 0;
      border-radius: 0px;
      width: 100%;
      height: 100vh;
  }

  .headerPanel{
      border-radius: 0;
      border-bottom-left-radius: 30px;
      border-bottom-right-radius: 30px;
      height: 180px;
      font-size: 1.5rem;
  }
  .iconPanel{
      display: none;
  }
  .controlPanel{
    height: 100%;
}
}